<template>
  <div class="_game-panel">
    <BallInfo :score="activePlayerScore" :ballColor="activePlayerBallColor" />
    <TimerMenu />
    <GameMenu />
  </div>
</template>

<script>
import { mapGetters, mapActions } from "@/store/modules/normalGame";
import * as getters from "@/store/modules/normalGame/getters";
import * as actions from "@/store/modules/normalGame/types";
import BallInfo from "@/components/info/BallInfo.vue";
import TimerMenu from "@/components/menu/TimerMenu.vue";
import GameMenu from "@/components/menu/GameMenu.vue";
import { ballColor } from "../../constant";

export default {
  name: "GamePanel",
  components: {
    BallInfo,
    TimerMenu,
    GameMenu
  },
  props: {
    game: {
      type: Object,
      default: null
    }
  },
  data: () => {
    return {};
  },
  computed: {
    ...mapGetters({
      activePlayerInfo: getters.ACTIVE_PLAYER_INFO,
      participatingPlayersInfo: getters.PARTICIPATING_PLAYERS_INFO
    }),
    activePlayerScore() {
      return this.activePlayerInfo ? this.activePlayerInfo.currentScore : 0;
    },
    activePlayerBallColor() {
      const color = this.activePlayerInfo
        ? this.activePlayerInfo.ballColor
        : ballColor.YELLOW;
      return color;
    }
  },
  watch: {
    participatingPlayersInfo: {
      handler(v) {
        if (v.length <= 1) {
          this.checkGameEnd();
        }
      },
      deep: true
    }
  },
  methods: {
    ...mapActions([actions.CANCEL_TURN, actions.UPDATE_GAME_END_TIME]),
    checkGameEnd() {
      const result = window.confirm("게임을 종료하시겠습니까?");
      if (result) {
        this[actions.UPDATE_GAME_END_TIME](new Date());
      } else {
        this[actions.CANCEL_TURN]();
      }
    }
  }
};
</script>

<style scoped>
._game-panel {
  padding: 0 7px;
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

._current-score {
  position: relative;
  font-size: 35pt;
}
</style>
