<template>
  <div class="_main">
    <nav>
      <button class="_nav-button active">
        <router-link to="/select_player">간편 경기(일반) - 임시용</router-link>
      </button>
      <button class="_nav-button">
        <router-link to="/normal_game">간편 경기(일반)</router-link>
      </button>
      <button class="_nav-button deactive">게임 통계</button>
      <button class="_nav-button deactive">플레이어 관리</button>
    </nav>
    <div class="_mode-select-box">
      <div class="_mode-select-button-wrap">
        <div
          class="_mode-select-button _two-button layer-box"
          :id="gameMode.INDIVIDUAL_GAGE_TYPE1"
          @click="onClickGameMode"
        >
          2인 경기
          <p>
            <font-awesome-icon icon="user" class="_icon" />&nbsp;vs
            <font-awesome-icon icon="user" class="_icon" />
          </p>
        </div>
        <div
          class="_mode-select-button _two-button layer-box"
          :id="gameMode.INDIVIDUAL_GAGE_TYPE2"
          @click="onClickGameMode"
        >
          3인 경기
          <p>
            <font-awesome-icon icon="user" class="_icon" />&nbsp;vs
            <font-awesome-icon icon="user" class="_icon" />&nbsp;vs
            <font-awesome-icon icon="user" class="_icon" />
          </p>
        </div>
      </div>
      <div class="_mode-select-button-wrap">
        <div
          class="_mode-select-button layer-box"
          :id="gameMode.INDIVIDUAL_GAGE_TYPE3"
          @click="onClickGameMode"
        >
          4인 경기
          <p>
            <font-awesome-icon icon="user" class="_icon" />&nbsp;vs
            <font-awesome-icon icon="user" class="_icon" />&nbsp;vs
            <font-awesome-icon icon="user" class="_icon" />&nbsp;vs
