        </div>
      </div>
      <div class="_mode-select-button-wrap">
        <div
          class="_mode-select-button layer-box"
          :id="gameMode.TEAM_GAGE_TYPE1"
          @click="onClickGameMode"
        >
          2:2 팀 경기
          <p>
            <font-awesome-icon icon="user-friends" class="_icon fa-flip-horizontal" />&nbsp;vs
            <font-awesome-icon icon="user-friends" class="_icon" />
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { getLocalStorage } from "@/common/util.js";
import { gameMode, localStorageName } from "@/constant";
import { mapActions } from "@/store/modules/normalGame";
import * as actions from "@/store/modules/normalGame/types";
import { mapGetters } from "@/store/modules/normalGame";
import * as getters from "@/store/modules/normalGame/getters";
import { library } from "@fortawesome/fontawesome-svg-core";
import {
  faUser,
  faUserFriends,
  faBiohazard
} from "@fortawesome/free-solid-svg-icons";

library.add(faUser);
library.add(faBiohazard);
library.add(faUserFriends);

export default {
  name: "Main",
  data: () => {
    return {
      gameMode: gameMode
    };
  },
  computed: {
    ...mapGetters({
      users: getters.USERS
    })
  },
  methods: {
    ...mapActions([
      actions.UPDATE_GAME_MODE,
      actions.UPDATE_SHOT_TIME,
      actions.UPDATE_USERS
    ]),
    onClickGameMode: function(event) {
      this[actions.UPDATE_GAME_MODE](event.currentTarget.id);
      this[actions.UPDATE_SHOT_TIME](40);
      if (this.users && this.users.length > 0) {
        this.$router.push("/select_users");
      } else {
        this.$router.push("/simple_register");
      }
    }
  },
  mounted() {
    this[actions.UPDATE_USERS](getLocalStorage(localStorageName.USERS));
  }
};
</script>

