<template>
  <div class="_simple-game-register">
    <button type="submit" class="_submit-button" @click="onClickGameStart">게임 시작</button>
    <h3>간편 게임 등록</h3>
    <p>플레이어의 이름과 핸디(목표 점수)를 등록 해 주세요</p>
    <div class="_input-box" v-for="(player, index) in players" :key="index">
      <input type="text" class="_input _name" placeholder="이름" v-model="player.name" />
      <input type="number" class="_input _handy" placeholder="핸디(목표 점수)" v-model="player.handi" />
    </div>
  </div>
</template>
<script>
import { setLocalStorage } from "@/common/util.js";
import { gameMode, localStorageName } from "@/constant";
import { mapActions } from "@/store/modules/normalGame";
import * as actions from "@/store/modules/normalGame/types";
import { mapGetters } from "@/store/modules/normalGame";
import * as getters from "@/store/modules/normalGame/getters";
const uuidv4 = require("uuid/v4");

export default {
  name: "SimpleGameRegister",
  data: () => {
    return {
      players: []
    };
  },
  computed: {
    ...mapGetters({
      gameMode: getters.GAME_MODE
    }),
    playersNumber() {
      let playerNumber = 0;
      switch (this.gameMode) {
        case gameMode.INDIVIDUAL_GAGE_TYPE1:
          playerNumber = 2;
          break;

        case gameMode.INDIVIDUAL_GAGE_TYPE2:
          playerNumber = 3;
          break;

        case gameMode.INDIVIDUAL_GAGE_TYPE3:
          playerNumber = 4;
          break;

        case gameMode.TEAM_GAGE_TYPE1:
          playerNumber = 4;
          break;
      }
      return playerNumber;
    }
  },
  methods: {
    ...mapActions([actions.UPDATE_USERS]),
    onClickGameStart() {
      setLocalStorage(localStorageName.USERS, this.players);
      this[actions.UPDATE_USERS](this.players);
      this.$router.push("/normal_game");
    }
  },
  mounted() {
    for (let index = 0; index < this.playersNumber; index++) {
      this.players.push({
        id: uuidv4(),
        name: "",
        handi: null
      });
    }
  }
};
</script>

