<template>
  <div class="_ball-info" :style="ballStyle">
    <p class="_score">{{ score }}</p>
  </div>
</template>

<script>
import { ballColor } from "../../constant";

export default {
  name: "BallInfo",
  props: {
    score: {
      type: Number,
      default: 0
    },
    ballColor: {
      string: String,
      default: ballColor.YELLOW
    }
  },
  computed: {
    ballStyle() {
      return {
        backgroundColor: this.ballColor
      };
    }
  }
};
</script>

