<template>
  <div class="_turn-menu">
    <button
      class="_turn-menu-button back"
      :class="{ _disabled: !isPossibleCancel }"
      @click="cancelTurn"
    >
      <font-awesome-icon icon="arrow-left" />
    </button>
    <button class="_turn-menu-button next" @click="nextTurn">
      다음 턴
      <font-awesome-icon icon="arrow-right" />
    </button>
  </div>
</template>

<script>
import { eventBus } from "@/constant";
import { mapGetters, mapActions } from "@/store/modules/normalGame";
import * as getters from "@/store/modules/normalGame/getters";
import * as actions from "@/store/modules/normalGame/types";

import { library } from "@fortawesome/fontawesome-svg-core";
import { faArrowRight, faArrowLeft } from "@fortawesome/free-solid-svg-icons";

library.add(faArrowRight);
library.add(faArrowLeft);

export default {
  name: "TurnMenu",
  computed: {
    ...mapGetters({
      prevActivePlayer: getters.PREV_ACTIVE_PLAYER,
      isGameEnd: getters.IS_GAME_END
    }),
    isPossibleCancel() {
      return !this.isGameEnd && this.prevActivePlayer;
    }
  },
  methods: {
    ...mapActions([actions.CANCEL_TURN, actions.NEXT_TURN]),
    cancelTurn() {
      if (this.isGameEnd || !this.isPossibleCancel) return;
      this.$EventBus.$emit(eventBus.INIT_TIMER);
      this[actions.CANCEL_TURN]();
    },
    nextTurn() {
      if (this.isGameEnd) {
        alert("게임이 종료되었습니다.");
        return;
      }
      this.$EventBus.$emit(eventBus.INIT_TIMER);
      this[actions.NEXT_TURN]();
    }
  }
};
</script>

<style scoped>
._turn-menu {
  margin-top: 7px;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
}
._turn-menu-button {
  min-height: 50px;
  color: #f7f7f7;
}

.back {
  width: 23%;
  background-color: #6c757d;
}
.next {
  width: 74%;
  background-color: #335890;
}
._disabled {
  color: rgba(247, 247, 247, 0.1);
  box-shadow: none;
}
</style>
