export const GAME_MODE = "GAME_MODE";
export const USERS = "USERS";
export const ACTIVE_PLAYER = "ACTIVE_PLAYER";
export const ACTIVE_PLAYER_INFO = "ACTIVE_PLAYER_INFO";
export const PREV_ACTIVE_PLAYER = "PREV_ACTIVE_PLAYER";
export const PLAYERS_INFO = "PLAYERS_INFO";
export const PARTICIPATING_PLAYERS_INFO = "PARTICIPATING_PLAYERS_INFO";
export const IS_GAME_END = "IS_GAME_END";

const getPlayerInfo = player => {
  const totalScore = player.history.reduce((a, b) => a + (b["score"] || 0), 0);
  const history = player.history[0];
  let currentScore;
  let highrun;
  let hr;
  if (history) {
    currentScore = player.history[0].score;
    highrun = Math.max.apply(
      Math,
      player.history.map(function (o) {
        return o["score"];
      })
    );
    hr = player.history[0].hr;
  } else {
    currentScore = 0;
    highrun = 0;
    hr = 0;
  }

  return {
    id: player.id,
    name: player.name,
    handi: player.handi,
    playing: player.playing,
    ballColor: history ? player.history[0].ballColor : undefined,
    totalScore,
    currentScore,
    highrun,
    hr,
    average: (hr ? totalScore / hr : 0).toFixed(2)
  };
};

export default {
  [ACTIVE_PLAYER]: state =>
    state.players.find(player => {
      return player.id === state.turn.activePlayingId;
    }),
  [PREV_ACTIVE_PLAYER]: state =>
    state.players.find(player => {
      return player.id === state.turn.prevActivePlayingId;
    }),
  [ACTIVE_PLAYER_INFO]: (state, getters) => {
    const player = getters[ACTIVE_PLAYER];
    if (!player) {
      return null;
    }
    return getPlayerInfo(player);
  },
  [PLAYERS_INFO]: state => {
    return state.players.map(player => {
      return getPlayerInfo(player);
    });
  },
  [GAME_MODE]: state => {
    return state.gameSession.mode;
  },
  [PARTICIPATING_PLAYERS_INFO]: (state, getters) => {
    const playersInfo = getters[PLAYERS_INFO];
    return playersInfo.filter(player => {
      return player.playing;
    });
  },
  [IS_GAME_END]: state => {
    return !!state.gameSession.endDate;
  },
  [USERS]: state => {
    return state.users;
  }
};
