<template>
  <div class="_select-users layer-box">
    <h3>플레이어 선택</h3>
    <p>게임을 진행할 플레이어를 선택 해주세요.</p>
    <div v-for="user in users" :key="user.id">
      <input type="checkbox" :id="user.id" :value="user" v-model="selectedUsers" />
      <label for="user.id">{{ user.name }}</label>
    </div>
    <button type="submit" class="_submit-button" @click="onClickGameStart">게임 시작</button>
  </div>
</template>

<script>
import { mapGetters } from "@/store/modules/normalGame";
import * as getters from "@/store/modules/normalGame/getters";
import { mapActions } from "@/store/modules/normalGame";
import * as actions from "@/store/modules/normalGame/types";

export default {
  name: "SelectUsers",
  computed: {
    ...mapGetters({
      users: getters.USERS
    })
  },
  data: () => {
    return {
      selectedUsers: []
    };
  },
  methods: {
    ...mapActions([actions.UPDATE_USERS]),
    onClickGameStart() {
      this[actions.UPDATE_USERS](this.selectedUsers);
      this.$router.push("/normal_game");
    }
  },
  mounted() {}
};
</script>

<style lang="scss" scoped>
._select-users {
  height: 70vh;
  margin: 5vh auto;
  width: 90%;
  overflow: scroll;
  top: 7px;
}
</style>
