import * as todos from "./types";

// context === {dispatch: ƒ, commit: ƒ, getters: {…}, state: {…}, rootGetters: {…},…}
export default {
  [todos.UPDATE_USERS]({ commit }, users) {
    commit(todos.UPDATE_USERS, users);
  },
  [todos.CONFIGURE_PLAYERS]({ commit }) {
    commit(todos.CONFIGURE_PLAYERS);
  },
  [todos.UPDATE_PLAYERS]({ commit }, players) {
    commit(todos.UPDATE_PLAYERS, players);
  },
  [todos.UPDATE_GAME_MODE]({ commit }, gameMode) {
    commit(todos.UPDATE_GAME_MODE, gameMode);
  },
  [todos.UPDATE_SHOT_TIME]({ commit }, shotTime) {
    commit(todos.UPDATE_SHOT_TIME, shotTime);
  },
  [todos.UPDATE_GAME_START_TIME]({ commit }, time) {
    commit(todos.UPDATE_GAME_START_TIME, time);
  },
  [todos.UPDATE_GAME_END_TIME]({ commit }, time) {
    commit(todos.UPDATE_GAME_END_TIME, time);
  },
  [todos.SCORE]({ commit }, payload) {
    commit(todos.SCORE, payload.value);
  },
  [todos.CANCEL_TURN]({ commit }) {
    commit(todos.CANCEL_TURN);
  },
  [todos.NEXT_TURN]({ commit }) {
    commit(todos.NEXT_TURN);
  }
};
