<template>
  <div class="_game-menu">
    <button class="_game-menu-button" @click="checkGameEnd">게임 종료</button>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "@/store/modules/normalGame";
import * as getters from "@/store/modules/normalGame/getters";
import * as actions from "@/store/modules/normalGame/types";

export default {
  name: "GameMenu",
  computed: {
    ...mapGetters({
      isGameEnd: getters.IS_GAME_END
    })
  },
  methods: {
    ...mapActions([actions.UPDATE_GAME_END_TIME]),
    checkGameEnd() {
      if (this.isGameEnd) return;
      const result = window.confirm("게임을 종료하시겠습니까?");
      if (result) {
        this[actions.UPDATE_GAME_END_TIME](new Date());
      }
    }
  }
};
</script>

