<template>
  <div id="inspire">
    <div class="_container">
      <div class="_vertical-box">
        <PlayerPanel class="_left-box" />
        <GamePanel />
        <div class="_right-box">
          <StatusPanel />
          <HistoryPanel />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions } from "@/store/modules/normalGame";
import * as actions from "@/store/modules/normalGame/types";
import PlayerPanel from "@/components/panel/PlayerPanel.vue";
import GamePanel from "@/components/panel/GamePanel.vue";
import StatusPanel from "@/components/panel/StatusPanel.vue";
import HistoryPanel from "@/components/panel/HistoryPanel.vue";

export default {
  name: "NormalGame",
  components: {
    PlayerPanel,
    GamePanel,
    StatusPanel,
    HistoryPanel
  },
  data: () => {
    return {};
  },
  methods: {
    ...mapActions([
      actions.CONFIGURE_PLAYERS,
      actions.UPDATE_GAME_START_TIME,
      actions.UPDATE_GAME_END_TIME
    ])
  },
  mounted() {
    // TODO : 게임 시작 버튼을 누르면 호출하도록 변경 필요
    this[actions.UPDATE_GAME_START_TIME](new Date());
    this[actions.UPDATE_GAME_END_TIME](null);
    this[actions.CONFIGURE_PLAYERS]();
  }
};
</script>

