<template>
  <div class="_score-menu">
    <button class="_score-menu-button decrease" @click="onScore(-2)">-2</button>
    <button class="_score-menu-button decrease" @click="onScore(-1)">-1</button>
    <button class="_score-menu-button increase" @click="onScore(1)">+1</button>
    <button class="_score-menu-button increase" @click="onScore(2)">+2</button>
  </div>
</template>

<script>
import { eventBus } from "@/constant";
import { mapGetters, mapActions } from "@/store/modules/normalGame";
import * as getters from "@/store/modules/normalGame/getters";
import * as actions from "@/store/modules/normalGame/types";

export default {
  name: "ScoreMenu",
  computed: {
    ...mapGetters({
      isGameEnd: getters.IS_GAME_END
    })
  },
  methods: {
    ...mapActions([actions.SCORE]),
    onScore(value) {
      if (this.isGameEnd) {
        alert("게임이 종료되었습니다.");
        return;
      }
      this.$EventBus.$emit(eventBus.INIT_TIMER);
      this[actions.SCORE]({ value: value });
    }
  }
};
</script>

<style scoped>
._score-menu {
  margin-top: 7px;
  display: flex;
  justify-content: space-between;
}
._score-menu-button {
  width: 23%;
  min-height: 50px;
  color: #f7f7f7;
}

.increase {
  background-color: #10c469;
}
.decrease {
  background-color: #ff5b5b;
}
</style>
