import * as todos from "./types";
import { ballColor } from "../../../constant";
import { shuffle } from "../../../common/util";

const getActivePlayer = state => {
  return state.players.find(player => {
    return player.id === state.turn.activePlayingId;
  });
};

const getTotalScore = player => {
  const totalScore = player.history.reduce((a, b) => a + (b["score"] || 0), 0);
  return totalScore;
};
const toggleTurnBallColor = state => {
  state.turn.ballColor =
    state.turn.ballColor === ballColor.YELLOW
      ? ballColor.WHITE
      : ballColor.YELLOW;
};

const updateTurnHR = state => {
  const player = getActivePlayer(state);
  state.turn.hr = player.history.length + 1;
};

export default {
  [todos.CONFIGURE_PLAYERS](state) {
    const players = [...state.users.map(user => user)];
    shuffle(players);
    state.players = players.map(player => {
      return {
        ...player,
        playing: true,
        history: []
      };
    });

    // turn 정보 구성
    const ids = state.players.map(player => player.id);
    state.turn = {
      hr: 1,
      ballColor: ballColor.YELLOW,
      order: ids,
      activePlayingId: ids[0],
      prevActivePlayingId: null
    };

    // 첫번째 순서 플레이어의 이닝 히스토리 초기화
    state.players[0].history.push({
      hr: state.turn.hr,
      score: 0,
      ballColor: state.turn.ballColor
    });
  },

  [todos.UPDATE_USERS](state, users) {
    state.users = users;
  },

  [todos.UPDATE_PLAYERS](state, players) {
    state.players = players;
  },

  [todos.UPDATE_GAME_MODE](state, gameMode) {
    state.gameSession.mode = gameMode;
  },

  [todos.UPDATE_SHOT_TIME](state, shotTime) {
    state.gameSession.shotTime = shotTime;
  },

  [todos.UPDATE_GAME_START_TIME](state, time) {
    state.gameSession.startDate = time;
  },

  [todos.UPDATE_GAME_END_TIME](state, time) {
    state.gameSession.endDate = time;
  },

  [todos.SCORE](state, value) {
    const player = getActivePlayer(state);

    player.history[0].score += value;

    // score 경계처리
    if (player.history[0].score < 0) {
      player.history[0].score = 0;
    }

    const totalScore = getTotalScore(player);
    if (totalScore > player.handi) {
      player.history[0].score -= totalScore - player.handi;
    }

    state.turn.prevActivePlayingId = null;
  },
  [todos.CANCEL_TURN](state) {
    if (!state.turn.prevActivePlayingId) return;
    let player = getActivePlayer(state);
    player.history.shift();
    const backupActivePlayingId = state.turn.activePlayingId;
    state.turn.activePlayingId = state.turn.prevActivePlayingId;
    state.turn.prevActivePlayingId = null;

    updateTurnHR(state);
    toggleTurnBallColor(state);

    // 게임이 종료된 플레이어가 턴 취소 되었을 때
    player = getActivePlayer(state);
    if (!player.playing) {
      player.playing = true;
      const activeIdx = state.turn.order.findIndex(id => {
        return id === backupActivePlayingId;
      });
      state.turn.order.splice(activeIdx, 0, state.turn.activePlayingId);
    }
  },
  [todos.NEXT_TURN](state) {
    // 현재 Active Player Id를 backup
    state.turn.prevActivePlayingId = state.turn.activePlayingId;

    // 현재 Active Player의 게임 종료를 결정
    let player = getActivePlayer(state);
    const totalScore = getTotalScore(player);
    if (totalScore >= player.handi) {
      player.playing = false;
    }

    // 다음 순서 플레이어를 Active Player로 변경
    const activeIdx = state.turn.order.findIndex(id => {
      return state.turn.activePlayingId === id;
    });
    const nextIdx = (activeIdx + 1) % state.turn.order.length;
    const nextActivePlayerId = state.turn.order[nextIdx];
    state.turn.activePlayingId = nextActivePlayerId;

    if (!player.playing) {
      state.turn.order.splice(activeIdx, 1);
    }

    // 턴 설정 변경
    updateTurnHR(state);
    toggleTurnBallColor(state);

    // 턴 설정으로 새로운 순서의 플레이어 이닝 히스토리 추가
    player = getActivePlayer(state);
    player.history.unshift({
      hr: state.turn.hr,
      score: 0,
      ballColor: state.turn.ballColor
    });
  }
};
